
import { IDeprecatedOpenLinkOptions, VIOpenLinkOptions } from './IOpenLinkOptions';
import IOpenLinkOptions from './IOpenLinkOptions';
import validate from "../Validate/Validate";
import { CloseEvent, CloseReason, EventType } from './events';
import { EventEmitter } from 'events';
import { IBrowserMessage } from './messages';
import Exchange from '../Exchange/Exchange';

export default class Browser {

	private readonly events: EventEmitter = new EventEmitter();

	constructor(
		private readonly exchange: Exchange<void>,
	) {
	}

	public async open(uri: string, options: IOpenLinkOptions = {}) {
		validate({ uri }).required().uri();
		validate({ options }).required().object(VIOpenLinkOptions);

		const headlessMode = options.headlessMode ?? true;
		await this.exchange.postMessage({
			uri,
			...options,
			headlessMode,
			type: 'open',
		});
	}

	public async close() {
		await this.exchange.postMessage({
			type: 'close',
		});
	}

	/**
	 * Find out when the browser is closed.
	 * This can happen by an API call, by a user request or after a timeout.
	 *
	 * This doesn't fire between `open` calls or on subsequent `close` calls.
	 *
	 * @returns A callback which removes the listener.
	 */
	public onClose(listener: (event: CloseEvent) => void): () => void {
		validate({ listener }).required().function();

		this.events.addListener(EventType.CLOSE, listener);
		return () => {
			this.events.removeListener(EventType.CLOSE, listener);
		};
	}

	public handleMessageData(data: IBrowserMessage) {
		switch (data.type) {
			case this.exchange.messageNameToType('closed'):
				const event: CloseEvent = {
					type: EventType.CLOSE,
					reason: data.reason as CloseReason,
				};
				this.events.emit(EventType.CLOSE, event);
				break;
			default:
		}
	}

	/** @deprecated use sos.browser.open() instead */
	public async openLink(
		uri: string,
		options: IDeprecatedOpenLinkOptions = {},
	) {
		console.warn('sos.browser.openLink() is deprecated and will be removed in the future versions. Use sos.browser.open() instead.');

		validate({ uri }).required().uri();
		validate({ options }).required().object();

		await this.exchange.postMessage({
			uri,
			...options,
			headlessMode: false,
			type: 'open_link',
		});
	}
}
