import { EventEmitter } from 'events';
import IPostMessage from '../IPostMessage';
import {
	IStorageUnit,
	IFilePath,
	IFile,
	IHeaders,
	ICopyFileOptions,
	IMoveFileOptions,
	VIFilePath,
	VICopyFileOptions,
	VIMoveFileOptions
} from './types';
import StorageUnitsChangedMessage from './StorageUnitsChangedMessage';
import validate from "../Validate/Validate";

const EVENT_STORAGE_UNITS_CHANGED = 'storage_units_changed';

export default class FileSystem {

	public static MESSAGE_PREFIX: string = 'file_system';

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {
		this.eventEmitter = new EventEmitter();
	}

	public async listStorageUnits(): Promise<IStorageUnit[]> {
		const { storageUnits } = await this.postMessage({
			type: this.getMessage('list_storage_units'),
		});
		return storageUnits;
	}

	public onStorageUnitsChanged(listener: () => void) {
		validate({listener}).required().function();
		this.eventEmitter.addListener(EVENT_STORAGE_UNITS_CHANGED, listener);
	}

	public removeStorageUnitsChangedListener(listener: () => void) {
		validate({listener}).required().function();
		this.eventEmitter.removeListener(EVENT_STORAGE_UNITS_CHANGED, listener);
	}

	public removeAllListeners() {
		this.eventEmitter.removeAllListeners(EVENT_STORAGE_UNITS_CHANGED);
	}

	public async listFiles(directoryPath: IFilePath): Promise<IFilePath[]> {
		validate({directoryPath}).required().object(VIFilePath);
		const { files } = await this.postMessage({
			type: this.getMessage('list_files'),
			directoryPath,
		});
		return files;
	}

	public async exists(filePath: IFilePath): Promise<boolean> {
		validate({filePath}).required().object(VIFilePath);
		const { exists } = await this.postMessage({
			type: this.getMessage('exists'),
			filePath,
		});
		return exists;
	}

	public async getFile(filePath: IFilePath): Promise<IFile | null> {
		validate({filePath}).required().object(VIFilePath);
		const { file } = await this.postMessage({
			type: this.getMessage('get_file'),
			filePath,
		});
		return file;
	}

	public async writeFile(filePath: IFilePath, contents: string): Promise<void> {
		validate({filePath}).required().object(VIFilePath);
		await this.postMessage({
			type: this.getMessage('write_file'),
			filePath,
			contents,
		});
	}

	public async readFile(filePath: IFilePath): Promise<string> {
		validate({filePath}).required().object(VIFilePath);
		const { contents } = await this.postMessage({
			type: this.getMessage('read_file'),
			filePath,
		});
		return contents;
	}

	public async copyFile(sourceFilePath: IFilePath, destinationFilePath: IFilePath, options: ICopyFileOptions = {}): Promise<void> {
		validate({sourceFilePath}).required().object(VIFilePath);
		validate({destinationFilePath}).required().object(VIFilePath);
		validate({options}).required().object(VICopyFileOptions);
		await this.postMessage({
			type: this.getMessage('copy_file'),
			sourceFilePath,
			destinationFilePath,
			options,
		});
	}

	public async moveFile(sourceFilePath: IFilePath, destinationFilePath: IFilePath, options: IMoveFileOptions = {}): Promise<void> {
		validate({sourceFilePath}).required().object(VIFilePath);
		validate({destinationFilePath}).required().object(VIFilePath);
		validate({options}).required().object(VIMoveFileOptions);
		await this.postMessage({
			type: this.getMessage('move_file'),
			sourceFilePath,
			destinationFilePath,
			options,
		});
	}

	public async deleteFile(filePath: IFilePath, recursive: boolean): Promise<void> {
		validate({filePath}).required().object(VIFilePath);
		validate({recursive}).required().boolean();
		await this.postMessage({
			type: this.getMessage('delete_file'),
			filePath,
			recursive,
		});
	}

	public async downloadFile(filePath: IFilePath, sourceUri: string, headers?: IHeaders): Promise<void> {
		validate({filePath}).required().object(VIFilePath);
		validate({sourceUri}).required().uri();
		validate({headers}).object();
		await this.postMessage({
			type: this.getMessage('download_file'),
			filePath,
			sourceUri,
			headers,
		});
	}

	public async extractFile(archiveFilePath: IFilePath, destinationDirectoryPath: IFilePath, method: string): Promise<void> {
		validate({archiveFilePath}).required().object(VIFilePath);
		validate({destinationDirectoryPath}).object(VIFilePath);
		validate({method}).required().string();
		await this.postMessage({
			type: this.getMessage('extract_file'),
			archiveFilePath,
			destinationDirectoryPath,
			method,
		});
	}

	public async getFileChecksum(filePath: IFilePath, hashType: string): Promise<string> {
		validate({filePath}).required().object(VIFilePath);
		validate({hashType}).required().string();
		const { checksum } = await this.postMessage({
			type: this.getMessage('get_file_checksum'),
			filePath,
			hashType,
		});
		return checksum;
	}

	public async createDirectory(directoryPath: IFilePath): Promise<void> {
		validate({directoryPath}).required().object(VIFilePath);
		await this.postMessage({
			type: this.getMessage('create_directory'),
			directoryPath,
		});
	}

	public async isDirectory(filePath: IFilePath): Promise<boolean> {
		validate({filePath}).required().object(VIFilePath);
		const { isDirectory } = await this.postMessage({
			type: this.getMessage('is_directory'),
			filePath,
		});
		return isDirectory;
	}

	public async link(sourceFilePath: IFilePath, destinationFilePath: IFilePath): Promise<void> {
		validate({sourceFilePath}).required().object(VIFilePath);
		validate({destinationFilePath}).required().object(VIFilePath);
		await this.postMessage({
			type: this.getMessage('link'),
			sourceFilePath,
			destinationFilePath,
		});
	}

	public handleMessageData(data: StorageUnitsChangedMessage) {
		switch (data.type) {
			case this.getMessage('storage_units_changed'):
				this.eventEmitter.emit(EVENT_STORAGE_UNITS_CHANGED);
				break;
			default:
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + FileSystem.MESSAGE_PREFIX + '.' + name;
	}
}
