
import IPostMessage from '../../IPostMessage';
import ISavedFiles from './ISavedFiles';
import IFile from './IFile';
import ISavedContents from './ISavedContents';
import ISavedFileMessage from './ISavedFileMessage';
import ISavedContentMessage from './ISavedContentMessage';
import ILoadedFileMessage from './ILoadedFileMessage';
import ILoadedContentMessage from './ILoadedContentMessage';
import IListedFilesMessage from './IListedFilesMessage';
import IListedContentsMessage from './IListedContentsMessage';
import IValidatedChecksumMessage from "./IValidatedChecksumMessage";
import IChecksumMessage from "./IChecksumMessage";
import FileNotFoundError from '../../Error/FileNotFoundError';
import ErrorCodes from '../../Error/ErrorCodes';
import AppletOfflineCacheError from '../../Error/AppletOfflineCacheError';
import validate from "../../Validate/Validate";

export default class Cache {

	public static MESSAGE_PREFIX: string = 'cache';

	private savedFiles: ISavedFiles;
	private savedContents: ISavedContents;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<
			ISavedFileMessage
			& ISavedContentMessage
			& ILoadedFileMessage
			& ILoadedContentMessage
			& IListedFilesMessage
			& IListedContentsMessage
			& IChecksumMessage
			& IValidatedChecksumMessage
		>
	) {
		this.savedFiles = {};
		this.savedContents = {};
	}

	public async listFiles(): Promise<string[]> {
		const response = await this.postMessage({
			type: this.getMessage('list_files'),
		});
		return response.fileUids;
	}

	public async loadFile(uid: string): Promise<IFile> {
		validate({uid}).required().string();

		const response = await this.postMessage(
			{
				type: this.getMessage('load_file'),
				uid,
			}
		);
		this.savedFiles[uid] = response.loadedFile;
		return response.loadedFile;
	}

	public async saveFile(uid: string, uri: string, headers?: { [key: string]: string }) {
		this.checkUidValidity(uid);
		if (headers && !this.isHeaders(headers)) {
			throw new AppletOfflineCacheError({
				kind: 'appletOfflineCacheError',
				message: `Invalid headers ${headers}`,
				code: ErrorCodes.OFFLINE_CACHE_HEADERS_NOT_VALID
			});
		}

		const response = await this.postMessage(
			{
				type: this.getMessage('save_file'),
				uid,
				uri,
				headers,
			}
		);
		this.savedFiles[uid] = response.savedFile;
	}

	public async loadOrSaveFile(uid: string, uri: string, headers?: { [key: string]: string }): Promise<IFile> {
		validate({uid}).required().string();
		validate({uri}).required().uri();

		try {
			return this.getCachedFile(uid);
		} catch (error) {
			try {
				return await this.loadFile(uid);
			} catch (error) {
				await this.saveFile(uid, uri, headers);
				return this.getCachedFile(uid);
			}
		}
	}

	public async deleteFile(uid: string) {
		this.checkUidValidity(uid);
		await this.postMessage({
			type: this.getMessage('delete_file'),
			uid,
		});
		delete this.savedFiles[uid];
	}

	public async listContents(): Promise<string[]> {
		const response = await this.postMessage({
			type: this.getMessage('list_contents'),
		});
		return response.contentUids;
	}

	public async loadContent(uid: string): Promise<string> {
		const response = await this.postMessage(
			{
				type: this.getMessage('load_content'),
				uid,
			}
		);
		this.savedContents[uid] = response.loadedContent;
		return response.loadedContent;
	}

	public async saveContent(uid: string, content: string) {
		this.checkUidValidity(uid);
		validate({content}).required().string();
		const response = await this.postMessage(
			{
				type: this.getMessage('save_content'),
				uid,
				content: '' + content,
			}
		);
		this.savedContents[uid] = response.savedContent;
	}

	public async deleteContent(uid: string) {
		this.checkUidValidity(uid);
		await this.postMessage({
			type: this.getMessage('delete_content'),
			uid,
		});
		delete this.savedContents[uid];
	}

	public async validateChecksumFile(uid: string, hash: string, hashType: string): Promise<boolean> {
		this.checkUidValidity(uid);
		validate({hash}).required().string();
		validate({hashType}).required().string();
		const response = await this.postMessage(
			{
				type: this.getMessage('validate_checksum'),
				uid,
				hash,
				hashType,
			}
		);
		return response.validatedChecksum;
	}

	public async getChecksumFile(uid: string, hashType: string): Promise<string> {
		this.checkUidValidity(uid);
		validate({hashType}).required().string();
		const response = await this.postMessage(
			{
				type: this.getMessage('get_checksum'),
				uid,
				hashType,
			}
		);
		return response.checksum;
	}

	public async decompressFile(uid: string, destinationUid: string, method: 'zip') {
		this.checkUidValidity(uid);
		validate({destinationUid}).required().string();
		await this.postMessage(
			{
				type: this.getMessage('decompress_file'),
				uid,
				destinationUid,
				method,
			}
		);
	}

	private getCachedFile(uid: string): IFile {
		if (typeof this.savedFiles[uid] === 'undefined') {
			throw new FileNotFoundError({
				kind: 'fileNotFoundError',
				message: 'File was not found ' + uid,
				code: ErrorCodes.FILE_NOT_FOUND_IN_CACHE
			});
		}
		return this.savedFiles[uid];
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Cache.MESSAGE_PREFIX + '.' + name;
	}

	private checkUidValidity(uid: string) {
		const regex = /^[^\\:*"<>|]+$/g; // Allow all characters allowed by all OSes in fileName + slash /

		validate({uid}).required().string().matchRegExp(regex, ErrorCodes.OFFLINE_CACHE_UID_NOT_VALID);

		return true;
	}

	private isHeaders(headers: any): boolean {
		if (typeof headers !== 'object') {
			return false;
		}
		Object.getOwnPropertyNames(headers).forEach(
			function (val: any) {
				if (typeof val !== 'string' || typeof headers[val] !== 'string') {
					return false;
				}
			}
		);
		return true;
	}
}
