import IPostMessage from '../IPostMessage';

export interface IMonitor {
	manufacturer: string;
	model: string;
	serial: string;
	firmware?: string;
}

class Monitors {

	public static MESSAGE_PREFIX: string = 'monitors';

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {}

	public async getList(): Promise<IMonitor[]> {
		const { monitors } = await this.postMessage({
			type: this.getMessage('get_list'),
		});
		return monitors;
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Monitors.MESSAGE_PREFIX + '.' + name;
	}
}

export default Monitors;
