import { EventEmitter } from 'events';
import IPostMessage from '../IPostMessage';
import IWifiMessage, { WifiEvent } from './IWifiEvent';
import validate from "../Validate/Validate";

export default class Wifi {

	public static MESSAGE_PREFIX: string = 'wifi';

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {
		this.eventEmitter = new EventEmitter();
	}

	public async isClientEnabled(): Promise<boolean> {
		const { isWifiEnabled } = await this.postMessage({
			type: this.getMessage('is_enabled'),
		});
		return isWifiEnabled;
	}

	public async enableClient(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('enable'),
		});
	}

	public async isAPEnabled(): Promise<boolean> {
		const { isWifiAPEnabled } = await this.postMessage({
			type: this.getMessage('is_ap_enabled'),
		});
		return isWifiAPEnabled;
	}

	public async enableAP(ssid: string, password: string): Promise<void> {
		validate({ssid}).required().string();
		validate({password}).required().string();
		await this.postMessage({
			type: this.getMessage('enable_ap'),
			ssid,
			password,
		});
	}

	public async disable() {
		await this.postMessage({
			type: this.getMessage('disable'),
		});
	}

	public async getConnectedTo(): Promise<string | null> {
		const { ssid } = await this.postMessage({
			type: this.getMessage('get_connected_to'),
		});
		return ssid || null;
	}

	public async connect(ssid: string, password?: string) {
		validate({ssid}).required().string();
		validate({password}).string();
		await this.postMessage({
			type: this.getMessage('connect'),
			ssid,
			password,
		});
	}

	public async disconnect() {
		await this.postMessage({
			type: this.getMessage('disconnect'),
		});
	}

	public async getCountry(): Promise<string | null> {
		const { countryCode } = await this.postMessage({
			type: this.getMessage('get_country'),
		});
		return countryCode || null;
	}

	public async setCountry(countryCode: string) {
		validate({countryCode}).required().string();
		await this.postMessage({
			type: this.getMessage('set_country'),
			countryCode,
		});
	}

	public async scanDevices(): Promise<{
		ssid: string;
		encrypted: boolean;
	}[]> {
		const { devices } = await this.postMessage({
			type: this.getMessage('scan'),
		});
		return devices;
	}

	public on(event: WifiEvent, listener: (...args: []) => void) {
		this.eventEmitter.on(event, listener);
	}

	public once(event: WifiEvent, listener: (...args: []) => void) {
		this.eventEmitter.once(event, listener);
	}

	public removeListener(event: WifiEvent, listener: (...args: []) => void) {
		this.eventEmitter.removeListener(event, listener);
	}

	public removeAllListeners(event?: WifiEvent) {
		if (event) {
			this.eventEmitter.removeAllListeners(event);
		} else {
			this.eventEmitter.removeAllListeners();
		}
	}

	public handleMessageData(data: IWifiMessage) {
		switch (data.type) {
			case this.getMessage('event_' + WifiEvent.CLIENT_ENABLED):
			case this.getMessage('event_' + WifiEvent.CLIENT_CONNECTED):
			case this.getMessage('event_' + WifiEvent.CLIENT_CONNECT_REJECTED):
			case this.getMessage('event_' + WifiEvent.CLIENT_DISCONNECTED):
			case this.getMessage('event_' + WifiEvent.AP_ENABLED):
			case this.getMessage('event_' + WifiEvent.DISABLED):
				const typeStringWithoutEvent = this.getMessage('event_');
				const event = data.type.substring(typeStringWithoutEvent.length);
				this.eventEmitter.emit(event);
				break;
			default:
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Wifi.MESSAGE_PREFIX + '.' + name;
	}
}
