import IPostMessage from '../IPostMessage';
import validate from "../Validate/Validate";

export default class Debug {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async enable(): Promise<void> {
		await this.setDebug(true);
	}

	public async disable(): Promise<void> {
		await this.setDebug(false);
	}

	public async isEnabled(): Promise<boolean> {
		const { isEnabled } = await this.postMessage({
			type: this.getMessage('is_enabled')
		});

		return isEnabled;
	}

	private async setDebug(enabled: boolean): Promise<void> {
		validate({enabled}).required().boolean();
		await this.postMessage({
			type: this.getMessage('set_debug'),
			enabled
		});
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
