import IPostMessage from '../IPostMessage';
import validate from "../Validate/Validate";

export default class Package {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async install(
		baseUrl: string,
		packageName: string,
		version: string,
		build: string | null
	): Promise<void> {
		validate({baseUrl}).required().uri();
		validate({packageName}).required().string();
		validate({version}).required().string();
		validate({build}).canBeNull().required().string();
		await this.postMessage({
			type: this.getMessage('install_package'),
			baseUrl,
			packageName,
			version,
			build
		});
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
