
import IPostMessage from '../../IPostMessage';
import ISetColorMessage from './ISetColorMessage';
import ErrorCodes from '../../Error/ErrorCodes';
import validate from "../../Validate/Validate";

export default class Led {

	public static MESSAGE_PREFIX: string = 'led';

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<ISetColorMessage>
	) {}

	public async setColor(color: string | null) {
		this.checkColorValidity(color);
		await this.postMessage(
			{
				type: this.getMessage('set_color'),
				color: color ? color.toUpperCase() : null,
			}
		);
	}

	private checkColorValidity(color: string | null) {
		validate({color}).canBeNull().string(null, ErrorCodes.HARDWARE_LED_COLOR_IS_NOT_STRING);
		validate({color}).canBeNull().matchRegExp(/^#[0-9A-Fa-f]{6,6}$/, ErrorCodes.HARDWARE_LED_COLOR_IS_NOT_VALID);
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Led.MESSAGE_PREFIX + '.' + name;
	}
}
