
import Display from '../Display/Display';

/**
 * If some transformer returns undefined, transforming message will not be forwarded to certain target
 * window and next transformers will not be called.
 */
export type Transformer = <T>(data: T, target: Window) => T | undefined;

export default class Iframe {

	private frontAppletWindows: Window[];

	constructor(
		private window: Window,
		private messagePrefix: string,
		private base: {
			apiJsUri: string;
			config: any | null;
			authHash: string | null;
			display: Display;
		},
		private transformers: Transformer[],
	) {
		this.frontAppletWindows = [];
	}

	public registerFrontAppletLoaderWindow(frontAppletWindow: Window) {
		frontAppletWindow.postMessage(
			{
				type: this.getMessage('api_js_uri'),
				uri: this.base.apiJsUri,
			},
			'*'
		);
	}

	public registerBundledFrontAppletWindow(frontAppletWindow: Window) {
		this.frontAppletWindows.push(frontAppletWindow);
		frontAppletWindow.postMessage(
			{
				type: this.getMessage('config.update'),
				config: this.base.config,
			},
			'*',
		);
		frontAppletWindow.postMessage(
			{
				type: this.getMessage('auth_hash.update'),
				authHash: this.base.authHash,
			},
			'*',
		);
		frontAppletWindow.postMessage(
			{
				type: this.getMessage('front_display_version.update'),
				version: this.base.display.version,
			},
			'*',
		);
	}

	public forwardToFrontAppletWindows(originalData: any) {
		for (let frontAppletWindow of this.frontAppletWindows) {
			const transformedData = this.transformers.reduce(
				(data: any, transformer: Transformer) => data ? transformer(data, frontAppletWindow) : data,
				originalData,
			);
			if (typeof transformedData === 'undefined') {
				continue;
			}
			frontAppletWindow.postMessage(transformedData, '*');
		}
	}

	public forwardFromFrontAppletWindows(frontAppletWindow: Window, data: any) {
		if (this.frontAppletWindows.indexOf(frontAppletWindow) !== -1) {
			this.window.parent.postMessage(data, '*');
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
