
type Color = string;

export type ITheme = {
	base?: 'light' | 'dark'; // Default light
	shape?: {
		cornerSize?: number; // Default 2
	};
	colors?: {
		primary?: Color;
		background?: Color;
		control?: Color;
	};
	widgets?: {
		editAddress?: {
			colors?: {
				background?: Color;
				text?: Color;
			};
		};
		buttonClose?: {
			colors?: {
				background?: Color | '@primary';
				text?: Color;
				icon?: Color;
			};
			icon?: 'none' | 'exit_to_app' | 'close' | 'cancel';
			text?: string;
		};
		progress?: {
			color?: Color | '@primary';
		};
	};
};

export const VITheme = {
	base: {'?string': ['light', 'dark']},
	shape: {'?object': {
			cornerSize: '?number',
		}
	},
	colors: {'?object': {
			primary: '?string',
			background: '?string',
			control: '?string',
		}
	},
	widgets: {'?object': {
			editAddress: {'?object': {
					colors: {'?object': {
							background: '?string',
							text: '?string',
						}
					},
				}
			},
			buttonClose: {'?object': {
					colors: {'?object': {
							background: '?string',
							text: '?string',
							icon: '?string',
						}
					},
					icon: {'?string': ['none', 'exit_to_app', 'close', 'cancel']},
					text: '?string',
				}
			},
			progress: {'?object': {
					color: '?string',
				}
			},
		}
	},
};

/** @deprecated */
export interface IDeprecatedOpenLinkOptions {
	acl?: RegExp[];
	aclMode?: 'blacklist' | 'whitelist';
	readOnlyAddressBar?: boolean;
	idleTimeout?: number; // milliseconds
	theme?: ITheme;
}

interface IOpenLinkOptions {
	aclDomains?: string[];
	aclMode?: 'blacklist' | 'whitelist';
	readOnlyAddressBar?: boolean;
	idleTimeout?: number; // milliseconds
	coordinates?: {
		x: number;
		y: number;
		width: number;
		height: number;
	};
	theme?: ITheme;
	headlessMode?: boolean;
	clearData?: boolean;
	canUserClose?: boolean;
}

export default IOpenLinkOptions;

export const VIOpenLinkOptions = {
	aclDomains: {'?array': 'string'},
	aclMode: {'?string': ['blacklist', 'whitelist']},
	readOnlyAddressBar: '?boolean',
	idleTimeout: '?number',
	coordinates: {'?object': {
			x: 'number',
			y: 'number',
			width: 'number',
			height: 'number',
		}
	},
	theme: {'?object': VITheme},
	headlessMode: '?boolean',
	clearData: '?boolean',
	canUserClose: '?boolean',
};
