import IPostMessage from '../IPostMessage';
import validate from "../Validate/Validate";

class Display {

	public static MESSAGE_PREFIX: string = 'display';

	public version: string | null = null;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {}

	public async supports(capability: string) {
		validate({capability}).string();
		const { supports } = await this.postMessage({
			type: this.getMessage('supports'),
			capability,
		});
		return supports;
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Display.MESSAGE_PREFIX + '.' + name;
	}
}

export default Display;
