import IPostMessage from '../IPostMessage';

export default class RemoteControl {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async enable(): Promise<void> {
		await this.setEnabled(true);
	}

	public async disable(): Promise<void> {
		await this.setEnabled(false);
	}

	public async isEnabled(): Promise<boolean> {
		const { isRemoteControlEnabled } = await this.postMessage({
			type: this.getMessage('get_is_remote_control_enabled')
		});

		return isRemoteControlEnabled;
	}

	public async lock(): Promise<void> {
		await this.disable();
	}

	public async unlock(): Promise<void> {
		await this.enable();
	}

	public isLocked(): Promise<boolean> {
		return this.isEnabled();
	}

	private async setEnabled(enabled: boolean): Promise<void> {
		await this.postMessage({
			type: this.getMessage('set_remote_control_enabled'),
			enabled
		});
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
