
import { EventEmitter } from 'events';
import IVideoEventListener from './IVideoEventListener';
import IVideoEvent, { EventType, EVENT_TYPES } from './IVideoEvent';
import IPostMessage from '../IPostMessage';
import IVideoMessage from './IVideoMessage';
import AppletVideoError from '../Error/AppletVideoError';
import ErrorCodes from '../Error/ErrorCodes';
import ErrorSuggestions from '../Error/ErrorSuggestions';
import SosError from '../Error/SosError';
import validate from "../Validate/Validate";

export interface IVideoOptions {
	'4k'?: boolean;
	background?: boolean;
	volume?: number;
}

export default class Video {

	public static MESSAGE_PREFIX: string = 'video';

	private eventEmitter: EventEmitter;
	private oncePromisesIndex: {
		[stringifiedSrcArguments: string]: { [eventType: string]: Promise<void> | undefined } | undefined;
	} = {};

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<void>
	) {
		this.eventEmitter = new EventEmitter();
	}

	public async play(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		this.removeAllVideoListeners(uri, x, y, width, height); // If not explicitly stopped and played again the same video
		const onceEndedPromise = this.createOncePromise('ended', uri, x, y, width, height);
		const onceStopPromise = this.createOncePromise('stop', uri, x, y, width, height);
		await this.postMessage({
			type: this.getMessage('play'),
			uri,
			x,
			y,
			width,
			height,
		});

		const srcArgumentsString = this.stringifySrcArguments(uri, x, y, width, height);
		this.oncePromisesIndex[srcArgumentsString] = {
			ended: onceEndedPromise,
			stop: onceStopPromise,
		};
		this.createAndEmitEvent('play', uri, x, y, width, height);
	}

	public async prepare(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
		options: IVideoOptions = {},
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		await this.postMessage({
			type: this.getMessage('prepare'),
			uri,
			x,
			y,
			width,
			height,
			options,
		});
		this.createAndEmitEvent('prepare', uri, x, y, width, height);
	}

	public async stop(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		await this.postMessage({
			type: this.getMessage('stop'),
			uri,
			x,
			y,
			width,
			height,
		});
		this.createAndEmitEvent('stop', uri, x, y, width, height);
		const srcArgumentsString = this.stringifySrcArguments(uri, x, y, width, height);
		delete this.oncePromisesIndex[srcArgumentsString];
	}

	public async pause(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		await this.postMessage({
			type: this.getMessage('pause'),
			uri,
			x,
			y,
			width,
			height,
		});
		this.createAndEmitEvent('pause', uri, x, y, width, height);
	}
	public async resume(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		await this.postMessage({
			type: this.getMessage('resume'),
			uri,
			x,
			y,
			width,
			height,
		});
		this.createAndEmitEvent('resume', uri, x, y, width, height);
	}

	public onceEnded(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		const srcArgumentsString = this.stringifySrcArguments(uri, x, y, width, height);
		const oncePromises = this.oncePromisesIndex[srcArgumentsString];
		if (typeof oncePromises === 'undefined') {
			throw new AppletVideoError({
				kind: 'appletVideoError',
				message: `This video was not played yet. The arguments: ${srcArgumentsString}`,
				code: ErrorCodes.VIDEO_WAS_NOT_PLAYED_YET,
				suggestion: ErrorSuggestions.VIDEO_WAS_NOT_PLAYED
			});
		}
		return oncePromises.ended;
	}

	public onceStop(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
	) {
		this.checkParamsValidity(uri, x, y, width, height);

		const srcArgumentsString = this.stringifySrcArguments(uri, x, y, width, height);
		const oncePromises = this.oncePromisesIndex[srcArgumentsString];
		if (typeof oncePromises === 'undefined') {
			throw new AppletVideoError({
				kind: 'appletVideoError',
				message: `This video was not played yet. The arguments: ${srcArgumentsString}`,
				code: ErrorCodes.VIDEO_WAS_NOT_PLAYED_YET,
				suggestion: ErrorSuggestions.VIDEO_WAS_NOT_PLAYED
			});
		}
		return oncePromises.stop;
	}

	public onPlay(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("play", listener);
	}

	public onPrepare(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("prepare", listener);
	}

	public onStop(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("stop", listener);
	}

	public onPause(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("pause", listener);
	}

	public onResume(listener: IVideoEventListener) {
		validate({listener}).required().function();

		this.eventEmitter.addListener("resume", listener);
	}

	public onEnded(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("ended", listener);
	}

	public onError(listener: IVideoEventListener) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("error", listener);
	}

	public handleMessageData(data: IVideoMessage) {
		switch (data.type) {
			case this.getMessage('ended'):
			case this.getMessage('error'):
				const type = data.type.substring(this.getMessage('').length) as 'ended' | 'error';
				this.createAndEmitEvent(type, data.uri, data.x, data.y, data.width, data.height);
				break;
			default:
		}
	}

	public removeEventListeners() {
		this.eventEmitter.removeAllListeners();
	}

	private createOncePromise(listenType: EventType, uri: string, x: number, y: number, width: number, height: number) {
		return new Promise<void>((resolve: () => void, reject: (error: Error) => void) => {
			const srcArguments = { uri, x, y, width, height };
			const listenEventName = this.getEventName({ type: listenType, srcArguments });
			const errorEventName = this.getEventName({ type: 'error', srcArguments });

			const listener = (event: IVideoEvent) => {
				if (JSON.stringify(event.srcArguments) === JSON.stringify({ uri, x, y, width, height })) {
					this.eventEmitter.removeListener(listenEventName, listener);
					this.eventEmitter.removeListener(errorEventName, listener);
					if (event.type === listenType) {
						resolve();
					} else
					if (event.type === 'error') {
						reject(new SosError({
							kind: 'sosError',
							message: JSON.stringify(event.srcArguments),
							code: ErrorCodes.UNEXPECTED_EXCEPTION
						}));
					}
				}
			};
			this.eventEmitter.addListener(listenEventName, listener);
			this.eventEmitter.addListener(errorEventName, listener);
		});
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Video.MESSAGE_PREFIX + '.' + name;
	}

	private removeAllVideoListeners(uri: string, x: number, y: number, width: number, height: number) {
		for (const type of EVENT_TYPES) {
			const eventName = this.getEventName({ type, srcArguments: { uri, x, y, width, height } });
			this.eventEmitter.removeAllListeners(eventName);
		}
	}

	private createAndEmitEvent(type: EventType, uri: string, x: number, y: number, width: number, height: number) {
		const videoEvent: IVideoEvent = {
			type,
			srcArguments: {
				uri,
				x,
				y,
				width,
				height,
			}
		};
		const eventName = this.getEventName(videoEvent);
		this.eventEmitter.emit(eventName, videoEvent);
		this.eventEmitter.emit(videoEvent.type, videoEvent);
	}

	private getEventName(videoEvent: IVideoEvent) {
		const srcArgumentsString = this.stringifySrcArguments(
			videoEvent.srcArguments.uri,
			videoEvent.srcArguments.x,
			videoEvent.srcArguments.y,
			videoEvent.srcArguments.width,
			videoEvent.srcArguments.height,
		);
		return `${videoEvent.type}_${srcArgumentsString}`;
	}

	private stringifySrcArguments(uri: string, x: number, y: number, width: number, height: number) {
		return `${uri}$${x}_${y}_${width}_${height}`;
	}

	private checkParamsValidity(uri: string, x: number, y: number, width: number, height: number) {
		validate({uri}).required().uri();
		validate({x}).required().number();
		validate({y}).required().number();
		validate({width}).required().number();
		validate({height}).required().number();
	}
}
