
export enum EventType {
	CLOSE = "close",
}

export const VIEventType = ['close'];

export type Event<TType extends EventType = EventType> = {
	type: TType,
};

export interface CloseEvent extends Event<EventType.CLOSE> {
	reason: CloseReason;
}

export enum CloseReason {
	API = "API",
	USER = "USER",
	TIMEOUT = "TIMEOUT",
}
