import IPostMessage from '../IPostMessage';
import { validatePinCode, generateRandomPin } from './helpers/SecurityHelper';

export default class Security {

	constructor(private messagePrefix: string, private postMessage: IPostMessage<any>) {}

	/**
	 * Get PIN code
	 * @returns 4 digits number string
	 */
	public async getPinCode(): Promise<string> {
		const { pinCode } = await this.postMessage({
			type: this.getMessage('get_pin_code'),
		});
		return pinCode;
	}

	/**
	 * Set PIN code
	 * @param pinCode 4 digits number string
	 */
	public async setPinCode(pinCode: string): Promise<void> {
		validatePinCode(pinCode);
		await this.postMessage({
			type: this.getMessage('set_pin_code'),
			pinCode,
		});
	}

	/**
	 * Generate 4 digits number string and set it as PIN code
	 */
	public async generateRandomPinCode(): Promise<void> {
		await this.setPinCode(generateRandomPin());
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
