
import createFrontApplet from './FrontApplet/createFrontApplet';
import FrontApplet from './FrontApplet/FrontApplet';
import Monitoring from './Monitoring/Montoring';

export const frontApplet = createFrontApplet(window, 'hug');
export const sos = frontApplet;
export const monitoring = new Monitoring(frontApplet);

declare global {
	interface Window {
		sos: FrontApplet;
		sosMonitoring: Monitoring;
	}
}

window.sos = frontApplet;
window.sosMonitoring = monitoring;

export default frontApplet;
module.exports = frontApplet;
module.exports.default = frontApplet;
