import IPostMessage from '../IPostMessage';
import IBatteryStatus from './IBatteryStatus';
import INetworkInfo from './INetworkInfo';
import Firmware from './Firmware';
import Audio from "./Audio";
import Screen from './Screen';
import RemoteControl from './RemoteControl';
import Time from './Time';
import App from './App';
import Debug from './Debug';
import Package from './Package';
import Power from './Power';
import Network from './Network';
import Wifi from './Wifi';
import Security from './Security';
import validate from "../Validate/Validate";

export default class Management {

	public static MESSAGE_PREFIX: string = 'management';

	public readonly app: App;
	public readonly audio: Audio;
	public readonly debug: Debug;
	public readonly firmware: Firmware;
	public readonly package: Package;
	public readonly power: Power;
	public readonly remoteControl: RemoteControl;
	public readonly screen: Screen;
	public readonly time: Time;
	public readonly network: Network;
	public readonly wifi: Wifi;
	public readonly security: Security;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>
	) {
		this.app = new App(this.getMessagePrefix(), this.postMessage);
		this.audio = new Audio(this.getMessagePrefix(), this.postMessage);
		this.debug = new Debug(this.getMessagePrefix(), this.postMessage);
		this.firmware = new Firmware(this.getMessagePrefix(), this.postMessage);
		this.package = new Package(this.getMessagePrefix(), this.postMessage);
		this.power = new Power(this.getMessagePrefix(), this.postMessage);
		this.remoteControl = new RemoteControl(this.getMessagePrefix(), this.postMessage);
		this.screen = new Screen(this.getMessagePrefix(), this.postMessage);
		this.time = new Time(this.getMessagePrefix(), this.postMessage);
		this.network = new Network(this.getMessagePrefix(), this.postMessage);
		this.wifi = new Wifi(this.getMessagePrefix(), this.postMessage);
		this.security = new Security(this.getMessagePrefix(), this.postMessage);
	}

	public async supports(capability: string): Promise<boolean> {
		validate({capability}).required().string();

		const { supports } = await this.postMessage({
			type: this.getMessage('supports'),
			capability,
		});

		return supports;
	}

	public async getModel(): Promise<string> {
		const { model } = await this.postMessage({
			type: this.getMessage('get_model')
		});

		return model;
	}

	public async getSerialNumber(): Promise<string> {
		const { serialNumber } = await this.postMessage({
			type: this.getMessage('get_serial_number')
		});

		return serialNumber;
	}

	/** @deprecated Use sos.management.network.getActiveInfo() instead */
	public async getNetworkInfo(): Promise<INetworkInfo> {
		return await this.network.getActiveInfo();
	}

	public async getBatteryStatus(): Promise<IBatteryStatus> {
		const { batteryStatus } = await this.postMessage({
			type: this.getMessage('get_battery_status')
		});

		return batteryStatus;
	}

	public async getTemperature(): Promise<number> {
		const { currentTemperature } = await this.postMessage({
			type: this.getMessage('get_current_temperature')
		});

		return currentTemperature;
	}

	public async resetSettings(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('reset_settings'),
		});
	}

	public async factoryReset(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('factory_reset'),
		});
	}

	private getMessage(name: string) {
		return this.getMessagePrefix() + '.' + name;
	}

	private getMessagePrefix() {
		return this.messagePrefix + '.' + Management.MESSAGE_PREFIX;
	}
}
