import { EventEmitter } from "events";
import IPostMessage from '../IPostMessage';
import Led from './Led/Led';
import SerialPort from './SerialPort';
import ISerialPortOptions from './ISerialPortOptions';
import ISerialPortDataMessage from './ISerialPortDataMessage';
import BarcodeScanner from './BarcodeScanner/BarcodeScanner';
import IBarcodeScannerDataMessage from './BarcodeScanner/IBarcodeScannerDataMessage';

export default class Hardware {

	public static MESSAGE_PREFIX: string = 'hardware';

	public readonly led: Led;
	public barcodeScanner: BarcodeScanner;

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {
		this.eventEmitter = new EventEmitter();
		this.led = new Led(this.getMessagePrefix(), this.postMessage);
		this.barcodeScanner = new BarcodeScanner(this.getMessagePrefix(), this.postMessage);
	}

	public async openSerialPort(options: ISerialPortOptions): Promise<SerialPort> {
		const { refid } = await this.postMessage({
			type: this.getMessage('serial_port.open'),
			options,
		});
		return new SerialPort(refid, this.eventEmitter, this.getMessagePrefix(), this.postMessage);
	}

	public handleMessageData(data: ISerialPortDataMessage | IBarcodeScannerDataMessage) {
		switch (data.type) {
			case this.getMessage('serial_port.data'):
				this.eventEmitter.emit('serial_port.data.' + data.refid, data.data);
				break;
			default:
				this.barcodeScanner.handleMessageData(data as IBarcodeScannerDataMessage);
		}
	}

	private getMessage(name: string) {
		return this.getMessagePrefix() + '.' + name;
	}

	private getMessagePrefix() {
		return this.messagePrefix + '.' + Hardware.MESSAGE_PREFIX;
	}
}
