
export interface IFontFormats {
	woff2: string;
	eot?: string;
	woff?: string;
	ttf?: string;
	svg?: string;
}

export interface IFontFaceProperties {
	fontFamily: string;
	fontStretch?: string;
	fontStyle?: string;
	fontWeight?: string;
	unicodeRange?: string;
}

export interface IAddFont extends IFontFaceProperties {
	uid: string;
	append: HTMLElement;
	formats: IFontFormats;
}

export const VIFontFormats = {
	woff2: 'string',
	eot: '?string',
	woff: '?string',
	ttf: '?string',
	svg: '?string',
};

export const VIFontFaceProperties = {
	fontFamily: 'string',
	fontStretch: '?string',
	fontStyle: '?string',
	fontWeight: '?string',
	unicodeRange: '?string',
};

export const VIAddFont = Object.assign(VIFontFaceProperties, {
	uid: 'string',
	append: 'object',
	formats: VIFontFormats,
});
