
import FrontApplet from '../FrontApplet/FrontApplet';
import { EnableMonitoring, DisableMonitoring } from './monitoringCommands';
import { TimingLoaded } from './Timing/timingCommands';
import { TakeHtmlSnapshot, HtmlSnapshotTaken } from './Html/htmlCommands';
import { ConsoleLogged } from './Console/consoleCommands';
import {
	OfflineCacheListFiles,
	OfflineCacheFilesListed,
	OfflineCacheLoadFile,
	OfflineCacheFileLoaded,
} from './Offline/Cache/offlineCacheCommands';
import { VideoStateChanged } from './Video/videoCommands';
import ICommandEvent from '../FrontApplet/Command/ICommandEvent';
import ICommand from '../FrontApplet/Command/ICommand';
import ICommandEventListener from '../FrontApplet/Command/ICommandEventListener';
import IVideoEvent from '../FrontApplet/Video/IVideoEvent';

export default class Monitoring {

	constructor(
		private frontApplet: FrontApplet,
	) {
		this.frontApplet.command.onCommand<EnableMonitoring>((commandEvent: ICommandEvent<EnableMonitoring>) => {
			if (commandEvent.command.type === EnableMonitoring) {
				this.frontApplet.config.sosMonitoring = true;
			}
		});
		this.frontApplet.command.onCommand<DisableMonitoring>((commandEvent: ICommandEvent<DisableMonitoring>) => {
			if (commandEvent.command.type === DisableMonitoring) {
				this.frontApplet.config.sosMonitoring = false;
			}
		});
		this.handleTimingLoaded();
		this.handleHtmlSnapshotTakingRequests();
		this.interceptConsoleLogging();
		this.handleOfflineCacheFilesRequests();
		this.handleVideoStatesChanges();
	}

	private onCommand<TCommand extends ICommand>(listener: ICommandEventListener<TCommand>) {
		this.frontApplet.command.onCommand((command: ICommandEvent<TCommand>) => {
			if (typeof this.frontApplet.config === 'object' && this.frontApplet.config.sosMonitoring) {
				listener(command);
			}
		});
	}

	private dispatch<TCommand extends ICommand>(command: TCommand) {
		if (typeof this.frontApplet.config === 'object' && this.frontApplet.config.sosMonitoring) {
			this.frontApplet.command.dispatch<TCommand>(command);
		}
	}

	private async handleTimingLoaded() {
		await this.frontApplet.onReady();
		this.dispatch<TimingLoaded>({ type: TimingLoaded });
	}

	private handleHtmlSnapshotTakingRequests() {
		this.onCommand((commandEvent: ICommandEvent<TakeHtmlSnapshot>) => {
			switch (commandEvent.command.type) {
				case TakeHtmlSnapshot:
					this.dispatch<HtmlSnapshotTaken>({
						type: HtmlSnapshotTaken,
						html: window.document.documentElement.outerHTML,
					});
					break;
				default:
			}
		});
	}

	private interceptConsoleLogging() {
		const consoleLog = console.log;
		console.log = (...messages: string[]) => {
			consoleLog.apply(console, messages);
			this.dispatch<ConsoleLogged>({
				type: ConsoleLogged,
				level: 'log',
				messages,
			});
		};

		const consoleError = console.error;
		console.error = (...messages: string[]) => {
			consoleError.apply(console, messages);
			this.dispatch<ConsoleLogged>({
				type: ConsoleLogged,
				level: 'error',
				messages,
			});
		};

		const consoleWarn = console.warn;
		console.warn = (...messages: string[]) => {
			consoleWarn.apply(console, messages);
			this.dispatch<ConsoleLogged>({
				type: ConsoleLogged,
				level: 'warn',
				messages,
			});
		};

		const consoleInfo = console.info;
		console.info = (...messages: string[]) => {
			consoleInfo.apply(console, messages);
			this.dispatch<ConsoleLogged>({
				type: ConsoleLogged,
				level: 'info',
				messages,
			});
		};

		const consoleDebug = console.debug;
		console.debug = (...messages: string[]) => {
			consoleDebug.apply(console, messages);
			this.dispatch<ConsoleLogged>({
				type: ConsoleLogged,
				level: 'debug',
				messages,
			});
		};
	}

	private handleOfflineCacheFilesRequests() {
		this.onCommand(async (commandEvent: ICommandEvent<OfflineCacheListFiles | OfflineCacheLoadFile>) => {
			switch (commandEvent.command.type) {
				case OfflineCacheListFiles:
					this.dispatch<OfflineCacheFilesListed>({
						type: OfflineCacheFilesListed,
						fileUids: await this.frontApplet.offline.cache.listFiles(),
					});
					break;
				case OfflineCacheLoadFile:
					this.dispatch<OfflineCacheFileLoaded>({
						type: OfflineCacheFileLoaded,
						file: await this.frontApplet.offline.cache.loadFile(commandEvent.command.uid),
					});
					break;
				default:
			}
		});
	}

	private handleVideoStatesChanges() {
		this.frontApplet.video.onPlay((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'play',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onPrepare((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'prepare',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onStop((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'stop',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onPause((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'pause',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onResume((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'resume',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onEnded((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'ended',
				...event.srcArguments,
			});
		});
		this.frontApplet.video.onError((event: IVideoEvent) => {
			this.dispatch<VideoStateChanged>({
				type: VideoStateChanged,
				state: 'error',
				...event.srcArguments,
			});
		});
	}
}
