import { EventEmitter } from 'events';
import IMessage from '../IMessage';
import validate from "../Validate/Validate";

export interface IStateChangeMessage extends IMessage {
	detected: boolean;
}

export default class Proximity {

	public static MESSAGE_PREFIX: string = 'proximity';

	private eventEmitter: EventEmitter;
	private lastDetected: boolean = false;

	constructor(
		private messagePrefix: string,
	) {
		this.eventEmitter = new EventEmitter();
	}

	public onStateChange(callback: (detected: boolean) => void) {
		validate({callback}).required().function();
		if (this.lastDetected) {
			callback(this.lastDetected);
		}
		this.eventEmitter.addListener('change', callback);
		return () => this.eventEmitter.removeListener('change', callback);
	}

	public handleMessageData(data: IStateChangeMessage) {
		switch (data.type) {
			case this.getMessage('change'):
				if (data.detected !== this.lastDetected) {
					this.lastDetected = data.detected;
					this.eventEmitter.emit('change', data.detected);
				}
				break;
			default:
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Proximity.MESSAGE_PREFIX + '.' + name;
	}
}
