import IPostMessage from '../../IPostMessage';
import ITimingTriggersMessage from './ITimingTriggersMessage';

export default class Triggers {

	public static MESSAGE_PREFIX: string = 'triggers';

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<ITimingTriggersMessage>,
	) {
	}

	public async pause() {
		await this.postMessage({ type: this.getMessage('pause') });
	}

	public async resume() {
		await this.postMessage({ type: this.getMessage('resume') });
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Triggers.MESSAGE_PREFIX + '.' + name;
	}
}
