
import { EventEmitter } from 'events';
import IKeyUpEventListener from './IKeyUpEventListener';
import IInputKeyMessage from './IInputKeyMessage';

export default class Input extends EventEmitter implements IInputEventEmitter {

	public static MESSAGE_PREFIX: string = 'input';

	constructor(
		public readonly window: Window,
		private messagePrefix: string,
	) {
		super();
	}

	public onKeyUp(listener: IKeyUpEventListener) {
		this.addListener('keyup', listener);
	}

	public handleMessageData(data: IInputKeyMessage) {
		switch (data.type) {
			case this.getMessage('keyup'):
				const keyupEvent = {
					type: 'keyup',
					keyCode: data.keyCode,
					keyName: data.keyName,
				};
				this.emit('keyup', keyupEvent);
				break;
			default:
		}
	}

	public removeEventListeners() {
		this.removeAllListeners();
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Input.MESSAGE_PREFIX + '.' + name;
	}
}

export declare class IInputEventEmitter {
	public on(event: 'keyup', listener: IKeyUpEventListener): this;
	public once(event: 'keyup', listener: IKeyUpEventListener): this;
	public addListener(event: 'keyup', listener: IKeyUpEventListener): this;
	public removeListener(event: 'keyup', listener: IKeyUpEventListener): this;
}
