import { EventEmitter } from "events";
import IPostMessage from '../IPostMessage';
import ISyncEvent from './ISyncEvent';
import ISyncSetValueMessage from './ISyncSetValueMessage';
import {
	ISyncSetValueEvent,
} from './syncEvents';
import validate from "../Validate/Validate";

export default class Sync {

	public static MESSAGE_PREFIX: string = 'sync';
	private static DEFAULT_GROUP_NAME: string = '____default____';

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>,
	) {
		this.eventEmitter = new EventEmitter();
	}

	public async connect(serverUri?: string) {
		validate({serverUri}).uri();
		await this.postMessage({
			type: this.getMessage('connect'),
			serverUri,
		});
	}

	public async close() {
		await this.postMessage({
			type: this.getMessage('close'),
		});
	}

	public async init(groupName: string = Sync.DEFAULT_GROUP_NAME) {
		validate({groupName}).required().string();
		await this.postMessage({
			type: this.getMessage('init'),
			groupName,
		});
	}

	public async wait(data?: any, groupName: string = Sync.DEFAULT_GROUP_NAME, timeout?: number) {
		validate({groupName}).required().string();
		validate({timeout}).number();
		const response = await this.postMessage({
			type: this.getMessage('wait'),
			groupName,
			data,
			timeout,
		});
		return response.responseData;
	}

	public async setValue(key: string, value: any, groupName: string = Sync.DEFAULT_GROUP_NAME) {
		validate({key}).required().string();
		validate({groupName}).required().string();
		await this.postMessage({
			type: this.getMessage('request_set_value'),
			groupName,
			key,
			value,
		});
	}

	public onValue(listener: (key: string, value: any, groupName?: string) => void) {
		validate({listener}).required().function();
		this.eventEmitter.addListener("set_value", (event: ISyncSetValueEvent) => {
			listener(event.key, event.value, event.groupName === Sync.DEFAULT_GROUP_NAME ? undefined : event.groupName);
		});
	}

	public handleMessageData(data: ISyncSetValueMessage) {
		switch (data.type) {
			case this.getMessage('set_value'):
				this.emitEvent('set_value', {
					type: 'set_value',
					groupName: data.groupName,
					key: data.key,
					value: data.value,
				} as ISyncSetValueEvent);
				break;
			default:
		}
	}

	public removeEventListeners() {
		this.eventEmitter.removeAllListeners();
	}

	private emitEvent<T extends string>(type: T, event: ISyncEvent<T>) {
		this.eventEmitter.emit(type, event);
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Sync.MESSAGE_PREFIX + '.' + name;
	}
}
