import IPostMessage from '../IPostMessage';
import IBrightness from './IBrightness';
import IOrientation from './IOrientation';
import { Orientation, Resolution } from './helpers/DeviceHelper';
import { Orientation as VideoOrientation } from './helpers/VideoHelper';
import validate  from "../Validate/Validate";

export default class Screen {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async resize(
		baseUrl: string,
		orientation: string,
		resolution: string,
		currentVersion: string,
		videoOrientation?: string,
	): Promise<void> {
		validate({baseUrl}).required().uri();
		validate({currentVersion}).required().string();
		if (typeof Orientation[orientation as keyof typeof Orientation] === 'undefined') {
			throw new Error('Invalid orientation');
		}
		if (typeof Resolution[resolution as keyof typeof Resolution] === 'undefined') {
			throw new Error('Invalid resolution');
		}
		if (videoOrientation && typeof VideoOrientation[videoOrientation as keyof typeof VideoOrientation] === 'undefined') {
			throw new Error('Invalid video orientation');
		}

		await this.postMessage({
			type: this.getMessage('resize_screen'),
			baseUrl,
			orientation,
			resolution,
			currentVersion,
			videoOrientation,
		});
	}

	public async getOrientation(): Promise<IOrientation> {
		const { screenOrientation } = await this.postMessage({
			type: this.getMessage('get_screen_orientation')
		});

		return screenOrientation;
	}

	public async setBrightness(
		timeFrom1: string,
		brightness1: number,
		timeFrom2: string,
		brightness2: number
	): Promise<void> {
		validate({brightness1}).required().number().min(0).max(100);
		validate({brightness2}).required().number().min(0).max(100);
		validate({timeFrom1}).canBeNull().required().string().matchRegExp(/\d{2}:\d{2}/).timerTime();
		validate({timeFrom2}).canBeNull().required().string().matchRegExp(/\d{2}:\d{2}/).timerTime();

		await this.postMessage({
			type: this.getMessage('set_screen_brightness'),
			timeFrom1,
			brightness1,
			timeFrom2,
			brightness2
		});
	}

	public async getBrightness(): Promise<IBrightness> {
		const { screenBrightness } = await this.postMessage({
			type: this.getMessage('get_screen_brightness')
		});

		return screenBrightness;
	}

	public async takeAndUploadScreenshot(uploadBaseUrl: string): Promise<string> {
		validate({uploadBaseUrl}).required().uri();
		const { screenshotUrl } = await this.postMessage({
			type: this.getMessage('upload_screenshot'),
			uploadBaseUrl
		});

		return screenshotUrl;
	}

	public async powerOn(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('power_on_display')
		});
	}

	public async powerOff(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('power_off_display')
		});
	}

	public async isPoweredOn(): Promise<boolean> {
		const { isDisplayPowerOn } = await this.postMessage({
			type: this.getMessage('get_is_display_power_on')
		});

		return isDisplayPowerOn;
	}

	private getMessage(name: string) {
		return this.getMessagePrefix() + '.' + name;
	}

	private getMessagePrefix() {
		return this.messagePrefix;
	}
}
