
export type NetworkInterface = 'wifi' | 'ethernet';

export const VNetworkInterface = ['wifi', 'ethernet'];

export default interface INetworkInfo {
	localAddress: string;
	ethernetMacAddress: string;
	wifiMacAddress: string;
	activeInterface: NetworkInterface;
	gateway?: string;
	netmask?: string;
	dns?: string[];
	interfaceName?: string;
}

export interface INetworkInterface {
	type: NetworkInterface;
	name: string;
	macAddress: string;
	localAddress?: string;
	gateway?: string;
	netmask?: string;
	dns?: string[];
}

export interface INetworkOptionsLegacy {
	interface: NetworkInterface;
	localAddress: string;
	gateway: string;
	netmask: string;
	dns: string[];
}

export interface INetworkOptions {
	localAddress: string;
	gateway: string;
	netmask: string;
	dns: string[];
}

export const VINetworkOptionsLegacy = {
	interface: {'string': VNetworkInterface},
	localAddress: 'string',
	gateway: 'string',
	netmask: 'string',
	dns: {'array': 'string'},
};

export const VINetworkOptions = {
	localAddress: 'string',
	gateway: 'string',
	netmask: 'string',
	dns: {'array': 'string'},
};
