
import Cache from './Cache/Cache';
import Types, { ITypes } from './Types';
import IFlagController, { IFlagControllerStatic } from './Flags/IFlagController';
import AppendFlagController from './Flags/AppendFlagController';
import IFlags from './Flags/IFlags';
import IFile from './Cache/IFile';
import IPostMessage from '../IPostMessage';
import ISaveFile, { VISaveFile } from './ISaveFile';
import ILoadFile from './LoadFile/ILoadFile';
import IFlag from './Flags/IFlag';
import IFileType from './IFileType';
import { ILoadFileControllerStatic } from './LoadFile/ILoadFileController';
import JavascriptLoadFileController from './LoadFile/JavascriptLoadFileController';
import CssLoadFileController from './LoadFile/CssLoadFileController';
import { IAddFont, IFontFormats, VIAddFont } from './IAddFont';
import { fontFaceGenerator, generateFontUid } from '../Font/fontFaceGenerator';
import validate from "../Validate/Validate";

export default class Offline {

	public static MESSAGE_PREFIX: string = 'offline';

	public readonly cache: Cache;
	public readonly types: ITypes;
	public readonly loadFile: ILoadFile;
	public readonly flags: IFlags;
	private flagControllers: { [name: string]: IFlagController };
	private FlagControllers: IFlagControllerStatic[] = [
		AppendFlagController,
	];
	private LoadFileControllers: ILoadFileControllerStatic[] = [
		JavascriptLoadFileController,
		CssLoadFileController,
	];

	constructor(
		private messagePrefix: string,
		private window: Window,
		private postMessage: IPostMessage<any>
	) {
		this.cache = new Cache(this.getMessagePrefix(), this.postMessage);
		this.types = Types;
		this.loadFile = this.LoadFileControllers.reduce(
			(loadFile: ILoadFile, LoadFileController: ILoadFileControllerStatic) => {
				const loadFileController = new LoadFileController(this.window);
				loadFile[loadFileController.name] = loadFileController;
				return loadFile;
			},
			{}
		);
		this.flagControllers = this.FlagControllers.reduce(
			(flagControllers: { [name: string]: IFlagController}, FlagController: IFlagControllerStatic) => {
				const flagController = new FlagController(this.loadFile);
				flagControllers[flagController.name] = flagController;
				return flagControllers;
			},
			{}
		);
		this.flags = Object.keys(this.flagControllers).reduce(
			(flags: IFlags, flagControllerName: string) => {
				const flagController = this.flagControllers[flagControllerName];
				flags[flagController.name] = (...args: any[]) => flagController.create(...args);
				return flags;
			},
			{
				append: null as any,
			}
		);
	}

	public addFiles(addFiles: ISaveFile[]) {
		validate({addFiles}).required().array('object');
		const promises = addFiles.map((addFile: ISaveFile) => this.addFile(addFile));
		return Promise.all(promises);
	}

	public async addFilesSync(addFiles: ISaveFile[]) {
		validate({addFiles}).required().array('object');

		for (let file of addFiles) {
			await this.addFile(file);
		}
	}

	public async addFile(addFile: ISaveFile) {
		validate({addFile}).required().object(VISaveFile);

		const savedFile = await this.cache.loadOrSaveFile(addFile.uid, addFile.uri, addFile.headers);

		const flags = addFile.flags || [];
		for (let flag of flags) {
			await this.handleFileFlag(flag, savedFile, addFile.type);
		}
	}

	public async addFont(addFont: IAddFont) {
		validate({addFont}).required().object(VIAddFont);
		const fontGenerator = fontFaceGenerator(this.window, addFont);

		const downloadingFonts = Object.keys(addFont.formats).map(async (fontExtension: keyof IFontFormats) => {
			const file = await this.cache.loadOrSaveFile(generateFontUid(addFont.uid, fontExtension), addFont.formats[fontExtension]!);
			fontGenerator.setFormat(fontExtension, file.filePath);
		});
		await Promise.all(downloadingFonts);

		const styleElement = fontGenerator.generateStyleElement();
		addFont.append.appendChild(styleElement);
	}

	private async handleFileFlag(flag: IFlag, file: IFile, fileType: IFileType) {
		const flagController = this.flagControllers[flag.type];
		if (typeof flagController === 'undefined') {
			throw new Error('Not implemented apply flag ' + flag.type);
		}
		const filePath = file.filePath;
		await flagController.applyFlag(flag, fileType, filePath);
	}

	private getMessagePrefix() {
		return this.messagePrefix + '.' + Offline.MESSAGE_PREFIX;
	}
}
