
import { EventEmitter } from 'events';
import ICommand from './ICommand';
import IPostMessage from '../IPostMessage';
import ICommandDispatchToAppletMessage from './ICommandDispatchToAppletMessage';
import ICommandEventListener from './ICommandEventListener';
import AppletCommandError from '../Error/AppletCommandError';
import ErrorCodes from '../Error/ErrorCodes';
import ErrorSuggestions from '../Error/ErrorSuggestions';
import validate from "../Validate/Validate";

export default class Command {

	public static MESSAGE_PREFIX: string = 'command';

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<void>
	) {
		this.eventEmitter = new EventEmitter();
	}

	public async dispatch<TCommand extends ICommand = ICommand>(command: TCommand) {
		this.checkTypeValidity(command.type);
		await this.postMessage({
			type: this.getMessage("dispatch"),
			command,
		});
	}

	public onCommand<TCommand extends ICommand = ICommand>(listener: ICommandEventListener<TCommand>) {
		validate({listener}).required().function();
		this.eventEmitter.addListener('command', listener);
	}

	public handleMessageData(data: ICommandDispatchToAppletMessage) {
		switch (data.type) {
			case this.getMessage('dispatch_to_applet'):
				const commandEvent = {
					type: 'command',
					command: data.command,
				};
				this.eventEmitter.emit('command', commandEvent);
				break;
			default:
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Command.MESSAGE_PREFIX + '.' + name;
	}

	private checkTypeValidity(type: string) {
		const regex = /^[a-zA-Z0-9\.\-_]+$/g;

		validate({type}).setError(
			new AppletCommandError({
				kind: 'appletCommandError',
				message: 'Type contains invalid characters.',
				code: ErrorCodes.COMMAND_TYPE_NOT_VALID,
				suggestion: ErrorSuggestions.COMMAND_TYPE_NOT_VALID,
			})
		).required().string().matchRegExp(regex);

		validate({type}).setError(
			new AppletCommandError({
				kind: 'appletCommandError',
				message: 'Type is longer then 100 characters.',
				code: ErrorCodes.COMMAND_TYPE_IS_TOO_LONG,
			})
		).lengthMax(100);
	}
}
