export interface IStorageUnit {
	type: string;
	capacity: number;
	freeSpace: number;
	usableSpace: number;
	removable: boolean;
}

export const VIStorageUnit = {
	type: 'string',
	capacity: 'number',
	freeSpace: 'number',
	usableSpace: 'number',
	removable: 'boolean',
};

export interface IFile {
	localUri: string;
	imageThumbnailUriTemplate?: string;
	videoThumbnailUriTemplate?: string;
	createdAt?: number;
	lastModifiedAt?: number;
	sizeBytes?: number;
	mimeType?: string;
}

export interface IVideoFile extends IFile {
	videoDurationMs?: number;
	videoResolution?: {
		width: number;
		height: number;
	};
	videoFramerate?: number;
	videoBitrate?: number;
	videoCodec?: string;
	audioBitrate?: number;
	audioCodec?: string;
}

export interface IHeaders {
	[key: string]: string;
}

export interface IFilePath {
	storageUnit: IStorageUnit;
	filePath: string;
}

export const VIFilePath = {
	storageUnit: {'object': VIStorageUnit},
	filePath: 'string',
};

export interface ICopyFileOptions {
	overwrite?: boolean;
}

export const VICopyFileOptions = {
	overwrite: '?boolean',
};

export interface IMoveFileOptions {
	overwrite?: boolean;
}

export const VIMoveFileOptions = {
	overwrite: '?boolean',
};
