import IPostMessage from '../IPostMessage';
import { TimerType, TimerWeekday, ITimer } from './helpers/TimerHelper';
import validate  from "../Validate/Validate";

export default class Power {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async systemReboot(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('reboot_system')
		});
	}

	public async appRestart(): Promise<void> {
		await this.postMessage({
			type: this.getMessage('restart_app')
		});
	}

	public async getTimers(): Promise<ITimer[]> {
		const { timers } = await this.postMessage({
			type: this.getMessage('get_timers'),
		});
		return timers;
	}

	public async setTimer(
		type: keyof typeof TimerType,
		timeOn: string | null,
		timeOff: string | null,
		weekdays: string[],
		volume: number
	): Promise<void> {
		if (typeof TimerType[type as keyof typeof TimerType] === 'undefined') {
			throw new Error('Invalid timer type');
		}
		validate({timeOn}).canBeNull().required().string().matchRegExp(/\d{2}:\d{2}:\d{2}/).timerTime();
		validate({timeOff}).canBeNull().required().string().matchRegExp(/\d{2}:\d{2}:\d{2}/).timerTime();
		validate({weekdays}).required().array('string');
		validate({volume}).required().number().min(0).max(100);
		for (const weekday of weekdays) {
			if (typeof TimerWeekday[weekday as keyof typeof TimerWeekday] === 'undefined') {
				throw new Error('Invalid timer weekday');
			}
		}

		await this.postMessage({
			type: this.getMessage('set_timer'),
			timerType: type,
			timeOn,
			timeOff,
			weekdays,
			volume
		});
	}

	public async unsetTimer(type: keyof typeof TimerType): Promise<void> {
		if (typeof TimerType[type as keyof typeof TimerType] === 'undefined') {
			console.warn('Invalid timer type');
		}
		await this.setTimer(type, null, null, [], 0);
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
