import IPostMessage from '../IPostMessage';
import validate from "../Validate/Validate";

export default class Audio {
	constructor(private messagePrefix: string, private postMessage: IPostMessage<any>) {
	}
	public async getVolume(): Promise<number> {
		const { volume } = await this.postMessage({
			type: this.getMessage('get_volume')
		});
		return volume;
	}
	public async setVolume(volume: number): Promise<void> {
		validate({volume}).required().number().min(0).max(100);
		await this.postMessage({
			type: this.getMessage('set_volume'),
			volume
		});
	}
	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
