import IPostMessage from '../IPostMessage';
import INetworkInfo, {
	INetworkInterface,
	INetworkOptions,
	INetworkOptionsLegacy,
	NetworkInterface,
	VINetworkOptions,
	VINetworkOptionsLegacy,
	VNetworkInterface,
} from './INetworkInfo';
import validate from "../Validate/Validate";

export default class Network {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	/** @deprecated use sos.management.network.listInterfaces() instead */
	public async getActiveInfo(): Promise<INetworkInfo> {
		const { networkInfo } = await this.postMessage({
			type: this.getMessage('get_network_info')
		});

		return networkInfo;
	}

	public async listInterfaces(): Promise<INetworkInterface[]> {
		const { interfaces } = await this.postMessage({
			type: this.getMessage('get_network_interfaces'),
		});
		return interfaces;
	}

	/** @deprecated use sos.management.network.setManual2() instead */
	public async setManual(options: INetworkOptionsLegacy): Promise<void>;

	/**
	 * Manually configure a network interface
	 * @param interfaceName can be retrieved from the list of interfaces returned by listInterfaces()
	 * @param options
	 */
	public async setManual(interfaceName: string, options: INetworkOptions): Promise<void>;

	public async setManual(...args: [string, INetworkOptions] | [INetworkOptionsLegacy]) {
		if (typeof args[0] === 'string') {
			const [interfaceName, options] = args;
			validate({ interfaceName }).required().string();
			validate({ options }).required().object(VINetworkOptions);
			await this.postMessage({
				type: this.getMessage('set_network_manual_v2'),
				interfaceName,
				options,
			});
		} else {
			const [options] = args;
			validate({ options }).required().object(VINetworkOptionsLegacy);
			await this.postMessage({
				type: this.getMessage('set_network_manual'),
				options,
			});
		}
	}

	/** @deprecated use sos.management.network.setDHCP2() instead */
	public async setDHCP(networkInterface: NetworkInterface): Promise<void>;

	/**
	 * Configure a network interface to use DHCP
	 * @param interfaceName can be retrieved from the list of interfaces returned by listInterfaces()
	 */
	public async setDHCP(interfaceName: string): Promise<void>;

	public async setDHCP(...args: [NetworkInterface] | [string]): Promise<void> {
		if (args[0] === 'ethernet' || args[0] === 'wifi') {
			const [networkInterface] = args;
			validate({ networkInterface }).required().string(VNetworkInterface);
			await this.postMessage({
				type: this.getMessage('set_network_dhcp'),
				networkInterface,
			});
		} else {
			const [interfaceName] = args;
			validate({ interfaceName }).required().string();
			await this.postMessage({
				type: this.getMessage('set_network_dhcp_v2'),
				interfaceName,
			});
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
