import { EventEmitter } from "events";
import IPostMessage from '../../IPostMessage';
import IBarcodeScannerDataMessage from './IBarcodeScannerDataMessage';

export const DEFAULT_SCANNER_ID = 0;

interface IBarcodeScannerOptions {
	timeout?: number;
	cancelPrevious?: boolean;
	scannerId?: number;
}

export default class BarcodeScanner {
	public static MESSAGE_PREFIX: 'barcode_scanner' = 'barcode_scanner';

	private eventEmitter: EventEmitter;

	constructor(private messagePrefix: string, private postMessage: IPostMessage<any>) {
		this.eventEmitter = new EventEmitter();
	}

	/**
	 * @experimental
	 */
	public async getVersion(): Promise<string> {
		const { version } = await this.postMessage({
			type: this.getMessage('get_version'),
		});
		return version;
	}

	/**
	 * @experimental
	 */
	public async start(userOptions?: Omit<IBarcodeScannerOptions, 'scannerId'>): Promise<{
		stop: () => Promise<void>;
		onData: (listener: (data: string) => void) => void;
		onError: (listener: (error: Error) => void) => void;
	}> {
		const defaultOptions = { scannerId: DEFAULT_SCANNER_ID, timeout: 10000, cancelPrevious: false };
		const { scannerId, ...options } = { ...defaultOptions, ...userOptions };
		if (options.cancelPrevious) {
			this.eventEmitter.removeAllListeners(`scanner_id_${scannerId}.data`);
			this.eventEmitter.removeAllListeners(`scanner_id_${scannerId}.error`);
		}
		await this.postMessage({
			type: this.getMessage('start'),
			scannerId,
			options,
		});

		const onData = (listener: (data: string) => void) => {
			this.eventEmitter.addListener(`scanner_id_${scannerId}.data`, listener);
		};
		const onError = (listener: (error: Error) => void) => {
			this.eventEmitter.addListener(`scanner_id_${scannerId}.error`, listener);
		};
		const stop = async () => {
			await this.postMessage({
				type: this.getMessage('stop'),
				scannerId,
			});
			this.eventEmitter.removeAllListeners(`scanner_id_${scannerId}.data`);
			this.eventEmitter.removeAllListeners(`scanner_id_${scannerId}.error`);
		};
		return { stop, onData, onError };
	}

	public handleMessageData(msg: IBarcodeScannerDataMessage): void {
		switch (msg.type) {
			case this.getMessage('data'):
				this.eventEmitter.emit(`scanner_id_${msg.scannerId}.data`, msg.data);
				break;
			case this.getMessage('error'):
				this.eventEmitter.emit(`scanner_id_${msg.scannerId}.error`, msg.data);
				break;
			default:
		}
	}

	private getMessage(name: string) {
		return [this.messagePrefix, BarcodeScanner.MESSAGE_PREFIX, name].join('.');
	}
}
