
import { EventEmitter } from 'events';
import { generateUniqueHash } from './Hash/generator';
import IMessage from './IMessage';
import Video from './Video/Video';
import Stream from './Stream/Stream';
import Offline from './Offline/Offline';
import ILoadFileController from './Offline/LoadFile/ILoadFileController';
import JavascriptLoadFileController from './Offline/LoadFile/JavascriptLoadFileController';
import Command from './Command/Command';
import Hardware from './Hardware/Hardware';
import Browser from './Browser/Browser';
import Iframe from './Iframe/Iframe';
import Touch from './Touch/Touch';
import Input from './Input/Input';
import Debug, { WeinreData } from './Debug/Debug';
import Display from './Display/Display';
import Timing from './Timing/Timing';
import Sync from './Sync/Sync';
import FileSystem from './FileSystem/FileSystem';
import { createError, ISosErrorTransferObject } from './Error/errorHelper';
import Management from './Management/Management';
import Monitors from './Monitors/Monitors';
import Sensors from './Sensors/Sensors';
import { createExchange } from './Exchange/Exchange';

export default class FrontApplet {

	public /*readonly*/ config: any | null;
	public /*readonly*/ authHash: string | null;
	public /*readonly*/ appletVersion: string | null;
	public readonly debug: Debug;
	public readonly display: Display;
	public readonly apiJsUri: string;
	public readonly stream: Stream;
	public readonly video: Video;
	public readonly command: Command;
	public readonly hardware: Hardware;
	public readonly offline: Offline;
	public readonly browser: Browser;
	public readonly iframe: Iframe;
	public readonly touch: Touch;
	public readonly input: Input;
	public readonly timing: Timing;
	public readonly sync: Sync;
	public readonly fileSystem: FileSystem;
	public readonly management: Management;
	public readonly monitors: Monitors;
	public readonly sensors: Sensors;

	private readyEmitter: EventEmitter;
	private invocationResponseEmitter: EventEmitter;
	private javascriptLoadFileController: ILoadFileController;

	constructor(
		public readonly window: Window & WeinreData,
		private messagePrefix: string
	) {
		const rootExchange = createExchange(messagePrefix, this.postMessage.bind(this));

		this.readyEmitter = new EventEmitter();
		this.invocationResponseEmitter = new EventEmitter();
		this.debug = new Debug(this.window, messagePrefix);
		this.display = new Display(messagePrefix, this.postMessage.bind(this));
		this.stream = new Stream(messagePrefix, this.postMessage.bind(this));
		this.video = new Video(messagePrefix, this.postMessage.bind(this));
		this.command = new Command(this.messagePrefix, this.postMessage.bind(this));
		this.hardware = new Hardware(this.messagePrefix, this.postMessage.bind(this));
		this.offline = new Offline(messagePrefix, this.window, this.postMessage.bind(this));
		this.browser = new Browser(rootExchange.createNamespaced('browser'));
		this.touch = new Touch(
			this.window,
			this.messagePrefix,
			this.postMessage.bind(this),
		);
		this.iframe = new Iframe(
			this.window,
			this.messagePrefix,
			this,
			[this.touch.transformIframeData.bind(this.touch)],
		);
		this.input = new Input(this.window, this.messagePrefix);
		this.timing = new Timing(this.messagePrefix, this.postMessage.bind(this));
		this.sync = new Sync(this.messagePrefix, this.postMessage.bind(this));
		this.fileSystem = new FileSystem(this.messagePrefix, this.postMessage.bind(this));
		this.management = new Management(messagePrefix, this.postMessage.bind(this));
		this.monitors = new Monitors(messagePrefix, this.postMessage.bind(this));
		this.sensors = new Sensors(messagePrefix);
		this.javascriptLoadFileController = new JavascriptLoadFileController(this.window);
		this.window.addEventListener('message', (event: MessageEvent) => this.handleMessage(event));
		this.postMessage({
			type: messagePrefix + '_api.ready'
		});
		this.startPinging();
	}

	public postMessage(data: IMessage) {
		const invocationUid = generateUniqueHash();
		this.window.setTimeout(() => void this.window.parent.postMessage(
			{
				invocationUid,
				...data,
			},
			'*'
		));
		return new Promise((resolve: (responseData: any) => void, reject: (error: any) => void) => {
			this.invocationResponseEmitter.once(invocationUid, (responseData: any) => {
				if (responseData.error) {
					if (responseData.error.error) {
						reject(createError(responseData.error.error as ISosErrorTransferObject));
					} else {
						reject(responseData.error);
					}
				} else {
					resolve(responseData.success);
				}
			});
		});
	}

	public onReady(listener?: () => void) {
		return new Promise<void>((resolve: () => void) => {
			this.readyEmitter.once('ready', () => {
				resolve();
				if (typeof listener !== 'undefined') {
					listener();
				}
			});
			this.checkIfReady();
		});
	}

	public restore() {
		this.postMessage({ type: this.getMessage('restore') });
		this.removeMediaListeners();
		this.forwardRestoreToFrontAppletWindows();
	}

	private forwardRestoreToFrontAppletWindows() {
		const data = {
			type: this.messagePrefix + 'restore',
		};
		this.iframe.forwardToFrontAppletWindows(data);
	}

	private removeMediaListeners() {
		this.video.removeEventListeners();
		this.stream.removeEventListeners();
		this.sync.removeEventListeners();
		this.input.removeEventListeners();
	}

	private checkIfReady() {
		if (
			typeof this.authHash !== 'undefined' &&
			typeof this.config !== 'undefined' &&
			this.display.version !== null
		) {
			this.readyEmitter.emit('ready');
		}
	}

	private handleMessage(event: MessageEvent) {
		if (typeof event.data === 'object') {
			if (event.source === this.window.parent) {
				const data = event.data;
				switch (data.type) {
					case this.getMessage('invocation.success'):
						this.invocationResponseEmitter.emit(data.invocationUid, { success: data });
						break;
					case this.getMessage('invocation.error'):
						this.invocationResponseEmitter.emit(data.invocationUid, { error: data });
						break;
					case this.getMessage('config.update'):
						this.config = data.config;
						this.checkIfReady();
						break;
					case this.getMessage('auth_hash.update'):
						this.authHash = data.authHash;
						this.checkIfReady();
						break;
					case this.getMessage('front_display_version.update'):
						this.display.version = data.version;
						this.checkIfReady();
						break;
					case this.getMessage('applet_version.update'):
						this.appletVersion = data.version;
						break;
					case this.getMessage('restore'):
						this.removeMediaListeners();
						break;
					case this.getMessage('assets.load_js'):
						this.javascriptLoadFileController.load(data.src, this.window.document.body);
						break;
					default:
						this.browser.handleMessageData(data);
						this.sync.handleMessageData(data);
						this.video.handleMessageData(data);
						this.stream.handleMessageData(data);
						this.input.handleMessageData(data);
						this.command.handleMessageData(data);
						this.fileSystem.handleMessageData(data);
						this.sensors.handleMessageData(data);
						this.management.wifi.handleMessageData(data);
						this.hardware.handleMessageData(data);
						this.touch.handleMessageData(data);
						this.debug.handleMessageData(data);
				}
				this.iframe.forwardToFrontAppletWindows(data);
			} else {
				const data = event.data;
				switch (data.type) {
					case this.messagePrefix + '_loader.ready':
						this.iframe.registerFrontAppletLoaderWindow(event.source! as Window);
						break;
					case this.messagePrefix + '_api.ready':
						this.iframe.registerBundledFrontAppletWindow(event.source! as Window);
						break;
					default:
						this.iframe.forwardFromFrontAppletWindows(event.source! as Window, data);
				}
			}
		}
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}

	private startPinging() {
		const doPing = () => this.postMessage({ type: this.getMessage('ping') });
		this.window.setInterval(doPing, 30e3);
		doPing();
	}
}
