import IFileType, { VIFileType } from './IFileType';
import IFlag, { VIFlag } from './Flags/IFlag';
import ErrorCodes from "../Error/ErrorCodes";
import ErrorCodesMessages from "../Error/ErrorCodesMessages";

export default ISaveFile;

interface ISaveFile {
	uid: string;
	uri: string;
	type: IFileType;
	headers?: { [key: string]: string };
	flags?: IFlag[];
}

export const VISaveFile = {
	uid: {'string': null, 'error': {
			code: ErrorCodes.FILE_DOES_NOT_HAVE_UID,
			message: ErrorCodesMessages[ErrorCodes.FILE_DOES_NOT_HAVE_UID],
		}
	},
	uri: 'string',
	type: {'string': VIFileType, 'error': {
			code: ErrorCodes.FILE_FLAGS_ARE_NOT_IN_ARRAY,
			message: ErrorCodesMessages[ErrorCodes.FILE_FLAGS_ARE_NOT_IN_ARRAY],
		}
	},
	headers: '?object',
	flags: {'?array': {
			'object': VIFlag,
			'error': {code: ErrorCodes.FILE_FLAG_IS_NOT_OBJECT, message: ErrorCodesMessages[ErrorCodes.FILE_FLAG_IS_NOT_OBJECT]}
		}
	},
};
