
import { EventEmitter } from 'events';
import IPostMessage from '../IPostMessage';
import StreamProtocol from './StreamProtocol';
import IStreamMessage from './IStreamMessage';
import IStreamEventListener from './IStreamEventListener';
import IStreamEvent from './IStreamEvent';
import {
	IStreamErrorEvent,
	IStreamConnectedEvent,
	IStreamDisconnectedEvent,
} from './streamEvents';
import AppletStreamError from '../Error/AppletStreamError';
import ErrorCodes from '../Error/ErrorCodes';
import validate from "../Validate/Validate";

export default class Stream {

	public static MESSAGE_PREFIX: string = 'stream';

	private eventEmitter: EventEmitter;

	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<void>,
	) {
		this.eventEmitter = new EventEmitter();
	}

	public prepare(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
		protocol: keyof typeof StreamProtocol,
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		return this.postMessage({
			type: this.getMessage('prepare'),
			uri, x, y, width, height, protocol,
		});
	}

	public play(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
		protocol?: keyof typeof StreamProtocol,
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		let uppercaseProtocol: string | undefined;
		if (typeof protocol !== 'undefined') {
			uppercaseProtocol = protocol.toUpperCase(); // backwards compatibility, old format was lowercase

			if (Object.keys(StreamProtocol).indexOf(uppercaseProtocol) < 0) {
				throw new AppletStreamError({
					kind: 'appletStreamError',
					message: 'Invalid stream protocol type: ' + protocol,
					code: ErrorCodes.STREAM_INVALID_PROTOCOL_TYPE
				});
			}
		}

		return this.postMessage({
			type: this.getMessage('play'),
			uri,
			x,
			y,
			width,
			height,
			protocol: uppercaseProtocol,
		});
	}

	public stop(
		uri: string,
		x: number,
		y: number,
		width: number,
		height: number,
	) {
		this.checkParamsValidity(uri, x, y, width, height);
		return this.postMessage({
			type: this.getMessage('stop'),
			uri,
			x,
			y,
			width,
			height,
		});
	}

	public onError(listener: IStreamEventListener<'error'>) {
		validate({listener}).required().function();

		this.eventEmitter.on('error', listener);
	}

	public onConnected(listener: IStreamEventListener<'connected'>) {
		validate({listener}).required().function();

		this.eventEmitter.on('connected', listener);
	}

	public onDisconnected(listener: IStreamEventListener<'disconnected'>) {
		validate({listener}).required().function();

		this.eventEmitter.on('disconnected', listener);
	}

	public handleMessageData(data: IStreamMessage) {
		switch (data.type) {
			case this.getMessage('error'):
				this.emitEvent('error', {
					type: 'error',
					srcArguments: {
						uri: data.uri,
						x: data.x,
						y: data.y,
						width: data.width,
						height: data.height,
						protocol: data.protocol,
					},
					errorMessage: data.errorMessage,
				} as IStreamErrorEvent);
				break;
			case this.getMessage('connected'):
				this.emitEvent('connected', {
					type: 'connected',
					srcArguments: {
						uri: data.uri,
						x: data.x,
						y: data.y,
						width: data.width,
						height: data.height,
						protocol: data.protocol,
					},
				} as IStreamConnectedEvent);
				break;
			case this.getMessage('disconnected'):
				this.emitEvent('disconnected', {
					type: 'disconnected',
					srcArguments: {
						uri: data.uri,
						x: data.x,
						y: data.y,
						width: data.width,
						height: data.height,
						protocol: data.protocol,
					},
				} as IStreamDisconnectedEvent);
				break;
			default:
		}
	}

	public removeEventListeners() {
		this.eventEmitter.removeAllListeners();
	}

	private emitEvent<T extends string>(type: T, event: IStreamEvent<T>) {
		this.eventEmitter.emit(type, event);
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + Stream.MESSAGE_PREFIX + '.' + name;
	}

	private checkParamsValidity(uri: string, x: number, y: number, width: number, height: number) {
		validate({uri}).required().uri();
		validate({x}).required().number();
		validate({y}).required().number();
		validate({width}).required().number();
		validate({height}).required().number();
	}
}
