import IPostMessage from '../IPostMessage';
import validate from "../Validate/Validate";

export default class Time {
	constructor(
		private messagePrefix: string,
		private postMessage: IPostMessage<any>) {
	}

	public async get(): Promise<{ currentDate: Date, timezone?: string, ntpServer?: string }> {
		const { currentTimeWithTimezone } = await this.postMessage({
			type: this.getMessage('get_current_time_with_timezone')
		});

		return currentTimeWithTimezone;
	}

	/** @deprecated use setManual instead */
	public set(currentDate: Date, timezone: string): Promise<void> {
		validate({currentDate}).required().date();
		validate({timezone}).required().string();
		return this.setManual(currentDate, timezone);
	}

	public async setManual(currentDate: Date, timezone: string): Promise<void> {
		validate({currentDate}).required().date();
		validate({timezone}).required().string();
		await this.postMessage({
			type: this.getMessage('set_current_time_with_timezone'),
			currentDate,
			timezone,
		});
	}

	public async setNTP(ntpServer: string, timezone: string): Promise<void> {
		validate({ntpServer}).required().string();
		validate({timezone}).required().string();
		await this.postMessage({
			type: this.getMessage('set_ntp_time_with_timezone'),
			ntpServer,
			timezone,
		});
	}

	private getMessage(name: string) {
		return this.messagePrefix + '.' + name;
	}
}
